import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';

import { AuthService } from '../../angular-services/auth.service';
import { UserService } from '../../angular-services/user.service';
import { User } from '../../classes/user';

@Component({
  selector: 'app-profile-form',
  templateUrl: './profile-form.component.html',
  styleUrls: ['./profile-form.component.css']
})
export class ProfileFormComponent implements OnInit {

  formProfile: FormGroup;
  user: User = new User();
  alert: any = { type: '', message: '', show: false };

  constructor(private authService: AuthService, private userService: UserService) { }

  ngOnInit() {
    this.initForm();
    this.getUserLogged();
  }

  getUserLogged() {
    this.authService.getSession().subscribe(
      response => {
        if (response !== undefined) {
          if (response.auth) {
            this.user = response.session[0];
            this.loadForm();
          }
        } else {
          if (localStorage.getItem('tokenUserLogged') !== null) {
            alert('Houve algum erro ao carregar os dados!');
          }
        }
      }
    )
  }

  initForm() {
    this.formProfile = new FormGroup({
      user: new FormControl('', Validators.required),
      password: new FormControl('', Validators.required),
      confirmPassword: new FormControl('', Validators.required)
    });
  }

  loadForm() {
    this.formProfile.patchValue({
      user: this.user.user
    });
  }

  save() {
    if (this.formProfile.valid) {
      let data = this.formProfile.value;
      if (data.password === data.confirmPassword) {
        delete data.confirmPassword;
        data.status = true;
        this.userService.updateUser(this.user._key, data).subscribe(
          response => {
            if (response !== undefined) {
              this.alert.type = 'success';
              this.alert.message = 'Dados alterados com sucesso!';
              this.alert.show = true;
            } else {
              alert('Houve algum erro ao salvar os dados!')
            }
          }
        )
      } else {
        this.alert.type = 'danger';
        this.alert.message = 'As senhas não conferem!';
        this.alert.show = true;
      }
    }
  }

}
